<script>
import axios from "axios";
import { baseUrl } from '@/utils/env'
// import commonHeader from "@/components/commonHeader/index.vue"
// import commonFooter from "@/components/commonFooter/index.vue"
export default{
    components:{
        // commonHeader,
        // commonFooter
    },
    data() {
        return{
            tableList:{},
        }
    },
    created() {
        this.getList()
    },
    methods:{
        handleMouseEnter(item, event) {
            const el = event.target;
            const isOverflowed = el.scrollWidth > el.clientWidth;
            
            if (isOverflowed) {
                item.showPopover = true;
            } else {
                item.showPopover = false;
            }
        },
        getList() {
            const apiUrl = `${baseUrl}/api/mfr/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                // console.log(response.data.rows);
                this.tableList = response.data.data
            })
            .catch(error => {
                
            });
        }
    }
}
</script>
