<template>
    <div class="d_container">
        <div class="d_content">
            <div v-if="$i18n.locale=='zh'">
                <div class="b_title">{{receivedData.title}}</div>
            </div>
            <div v-else>
                <div class="b_title">{{receivedData.titleEn}}</div>
            </div>
            <div class="tline"></div>
            <div v-if="$i18n.locale=='zh'">
                <div class="b_content" v-html="receivedData.content"></div>
            </div>
            <div v-else>
                <div class="b_content" v-html="receivedData.contentEn"></div>
            </div>
            <!-- <img src="@/assets/aboutus/swiper/s_1.jpg" alt=""> -->
        </div>
        <div class="d_line"></div>
    </div>
</template>
<script>
export default{
    components:{
    },
    data() {
        return{
            receivedData:{}
        }
    },
    created() {
        // 从本地存储中读取数据
        const storedData = localStorage.getItem('content');
        if (storedData) {
            this.receivedData = JSON.parse(storedData);
        }
    },
    methods:{
    }
}
</script>
