<template>
    <div>
        <!-- <commonHeader></commonHeader> -->
        <div class="a_main">
            <div class="a_header">
                <img src="../../assets/applications/a_1.jpg" alt="" style="width:100%;height:100%;">
            </div>
            <div class="a_content">
                <div class="a_title" v-if="$route.query.title=='1'">{{$t('h.fl4_1')}}</div>
                <div class="a_title" v-if="$route.query.title=='2'">{{$t('h.fl4_2')}}</div>
                <div class="a_top">
                    <div class="t_main" @click="clickH" v-if="$route.query.title=='2'||!$route.query.title">
                        <img src="../../assets/applications/t_1.jpg" alt="" @mouseenter="show('t_1')" @mouseleave="hide('t_1')">
                        <div class="t_container" v-show="topone" @mouseenter="show('t_1')" @mouseleave="hide('t_1')">
                            <div>{{$t("h.t_1")}}</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                    <div class="t_main" @click="clickIC" v-if="$route.query.title=='1'||!$route.query.title">
                        <img src="../../assets/applications/t_2.jpg" alt="" @mouseenter="show('t_2')" @mouseleave="hide('t_2')">
                        <div class="t_container" v-show="toptwo" @mouseenter="show('t_2')" @mouseleave="hide('t_2')">
                            <div>{{$t("h.t_2")}}</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                    <div class="t_main" @click="clickME" v-if="$route.query.title=='1'||!$route.query.title">
                        <img src="../../assets/applications/t_3.jpg" alt="" @mouseenter="show('t_3')" @mouseleave="hide('t_3')">
                        <div class="t_container" v-show="topthree" @mouseenter="show('t_3')" @mouseleave="hide('t_3')">
                            <div>{{$t("h.t_3")}}</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                    <div class="t_main" @click="clickAE" v-if="$route.query.title=='1'||!$route.query.title">
                        <img src="../../assets/applications/b_1.jpg" alt="" @mouseenter="show('b_1')" @mouseleave="hide('b_1')">
                        <div class="t_container" v-show="bottomone" @mouseenter="show('b_1')" @mouseleave="hide('b_1')">
                            <div>{{$t("h.b_1")}}</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                    <div class="t_main" @click="clickI" v-if="$route.query.title=='1'||!$route.query.title">
                        <img src="../../assets/applications/b_2.jpg" alt="" @mouseenter="show('b_2')" @mouseleave="hide('b_2')">
                        <div class="t_container" v-show="bottomtwo" @mouseenter="show('b_2')" @mouseleave="hide('b_2')">
                            <div>IOT</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                    <div class="t_main" @click="clickCN" v-if="$route.query.title=='1'||!$route.query.title">
                        <img src="../../assets/applications/b_3.jpg" alt="" @mouseenter="show('b_3')" @mouseleave="hide('b_3')">
                        <div class="t_container" v-show="bottomthree" @mouseenter="show('b_3')" @mouseleave="hide('b_3')">
                            <div>{{$t("h.b_3")}}</div>
                            <div>{{$t("h.seemore")}}>></div>
                        </div>
                    </div>
                </div>
                <!-- <div class="a_bottom" v-if="$route.query.title=='1'||!$route.query.title">
                    
                </div> -->
            </div>
        </div>
        <!-- <commonFooter></commonFooter> -->
    </div>
</template>
<script>
// import commonHeader from "@/components/commonHeader/index.vue"
// import commonFooter from "@/components/commonFooter/index.vue"
export default{
    components:{
        // commonHeader,
        // commonFooter
    },
    data() {
        return{
            topone:false,
            toptwo:false,
            topthree:false,
            bottomone:false,
            bottomtwo:false,
            bottomthree:false,
        }
    },
    methods:{
        clickH() {
            this.$router.push({name:'hdetails'})
        },
        clickIC() {
            this.$router.push({name:'icdetails'})
        },
        clickME() {
            this.$router.push({name:'medetails'})
        },
        clickAE() {
            this.$router.push({name:'aedetails'})
        },
        clickI() {
            this.$router.push({name:'idetails'})
        },
        clickCN() {
            this.$router.push({name:'cndetails'})
        },
        show(val) {
            if(val=='t_1') {
                this.topone = true
            } else if(val=='t_2') {
                this.toptwo = true
            } else if(val=='t_3') {
                this.topthree = true
            } else if(val=='b_1') {
                this.bottomone = true
            } else if(val=='b_2') {
                this.bottomtwo = true
            } else if(val=='b_3') {
                this.bottomthree = true
            }
        },
        hide(val) {
            if(val=='t_1') {
                this.topone = false
            } else if(val=='t_2') {
                this.toptwo = false
            } else if(val=='t_3') {
                this.topthree = false
            } else if(val=='b_1') {
                this.bottomone = false
            } else if(val=='b_2') {
                this.bottomtwo = false
            } else if(val=='b_3') {
                this.bottomthree = false
            }
        },
    }
}
</script>
