        }
        .t_main {
            margin-right: 50px;
            position: relative;
            margin-bottom: 50px;
            &:nth-child(3){
                margin-right: 0;
            }
            &:last-child{
                margin-right: 0;
            }
        }
    }
    .a_bottom{
        display: flex;
        align-items: center;
        margin-top: 50px;
        display: flex;
