        color: #fff;
        background: rgba(0, 0, 0, 0.5);
        z-index: 1;
        cursor: pointer;
        div:first-child{
            font-family: Arial;
            font-weight: 400;
            font-size: 32px;
            color: #FFFFFF;
            text-align: center;
            padding-top:60px;
        }
        div:last-child{
            font-family: PingFang SC;
            font-weight: 400;
                font-size: 14px;
