<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <div id="app">
    <commonHeader></commonHeader>
    <router-view style="padding-top:120px"/> <!-- 路由内容将在这里渲染 -->
    <commonFooter></commonFooter>
    <el-backtop :bottom="70" :right="70" style="width:51px;height:50px">
      <div
        style="{
          background-color: rgba(0,0,0, 0.4);
          position:relative;
          z-index:1000;
          width:51px;height:50px
        }"
      >
      <img src="./assets/icon/backtop.png" alt="" style="width:51px;height:50px">
    </div>
    </el-backtop>
  </div>
</template>

<script>
import axios from "axios";
import { baseUrl } from '@/utils/env'
import commonHeader from "@/components/commonHeader/index.vue"
import commonFooter from "@/components/commonFooter/index.vue"

export default {
  name: 'App',
  components: {
    commonHeader,
    commonFooter
  },
  created() {
    this.getLocation()
  },  
  methods:{
    // 获取地区
    getLocation () {
      const apiUrl = `${baseUrl}/api/common/getIP2Language`;
      axios.get(apiUrl).then(res => {
        var locale = ''
        if (localStorage.getItem("locale")) {
          locale = localStorage.getItem("locale")
        } else {
          if (res.data.data == 'cn' || res.data.data == 'zh') {
            localStorage.setItem("locale", 'zh')
            locale = 'zh'
          } else {
            localStorage.setItem("locale", 'en')
            locale = 'en'
          }
        }
        this.$i18n.locale = locale;
      })
      .catch(error => {
          
      });
    },
  }
}
</script>

