    methods:{
        getList() {
            const apiUrl = `${baseUrl}/api/address/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                this.pointerList = response.data.data
            })
            .catch(error => {
                
            });
        },
        subMit() {
            if(this.form.customerName==''||this.form.customerName==undefined) {
                this.$message({
                  message: '请输入姓名',
                  type: 'info'
                });
                return
            }
            if(this.form.company==''||this.form.company==undefined) {
                this.$message({
                  message: '请输入公司名称',
                  type: 'info'
                });
                return
            }
            if(this.form.tel==''||this.form.tel==undefined) {
