            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                // console.log(response.data.rows);
                this.swiperList = response.data.rows
                let newDataListProd = []
                let currentProd = 0
                if(this.swiperList && this.swiperList.length>0){
                for(let i=0;i<=this.swiperList.length-1;i++){
                    if(i%3 !== 0 || i === 0 ){
                    if(!newDataListProd[currentProd]){
                        newDataListProd.push([this.swiperList[i]])
                    }else{
                        newDataListProd[currentProd].push(this.swiperList[i])
                    }
                    }else{
                        currentProd++
                    newDataListProd.push([this.swiperList[i]])
                    }
