<script>
import axios from "axios";
import { baseUrl } from '@/utils/env'
// import commonHeader from "@/components/commonHeader/index.vue"
// import commonFooter from "@/components/commonFooter/index.vue"
export default{
    components:{
        // commonHeader,
        // commonFooter
    },
    data() {
        return{
            tableList:{}
        }
    },
    created() {
        if(this.$i18n.locale=='zh') {
            if(this.$route.query.tip=='1') {
                document.title = "宏能电子 | 2022年度前50名"
            } else {
                document.title = "宏能电子 | 品牌介绍"
            }
        } else {
            if(this.$route.query.tip=='1') {
                document.title = "Top 50 Lines"
            } else {
                document.title = "Honeng Electronics | line Card"
            }
        }
        this.getList()
    },
    methods:{
        getList() {
            const apiUrl = `${baseUrl}/api/topMfr/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                // console.log(response.data.rows);
                this.tableList = response.data.data
            })
            .catch(error => {
                
            });
        }
    }
}
</script>
