<template>
    <div class="d_container">
        <div class="d_content">
            <div class="b_title" v-if="$i18n.locale=='zh'">全球贸易合规声明</div>
            <div class="b_title" v-else>Global Trade Compliance Statement</div>
            <div class="tline"></div>
            <!-- 显示使用模板字符串创建的多行字符串 -->
            <!-- <p :style="{ whiteSpace: 'pre-wrap' }">{{ text }}</p> -->
            <div v-if="$i18n.locale=='zh'">
                <div v-html="zhtext"></div>
            </div>
            <div v-else><div v-html="entext"></div></div>
        </div>
        <div class="d_line"></div>
    </div>
</template>
<script>
import axios from "axios";
import { baseUrl } from '@/utils/env'
export default{
    components:{
    },
    data() {
        return{
            title:'',
            text:'',
            entext:`HONENG ELECTRONICS CO., LTD (hereinafter referred to as " HONENG ELEC.") 
HONENG ELEC. company policy is to fully comply with all relevant laws and regulations administered by the countries in which HONENG ELEC. conducts global trade. Under no circumstances will HONENG ELEC. sell or ship product contrary to applicable export control laws and trade regulations. Given the specific nature of the goods HONENG ELEC. sells, we make the following special statement: HONENG ELEC.’s distributors, resellers, customers, and end-users are responsible for compliance with U.S. export control and import laws and regulations when exporting, re-exporting, importing, or otherwise transferring any HONENG ELEC. products or technology, including products derived from or based on such technology (collectively, “HONENG ELEC. Products”). These laws impose restrictions on the shipment or transfer of HONENG ELEC. Products to certain countries and persons, and/or on the ultimate end-uses of such products. It is you or your organization’s responsibility to be aware of applicable export control and import restrictions and comply with them. HONENG ELEC. assumes no responsibility or liability for your and your organization’s failure to comply. Failure to comply with U.S. and non-U.S. export control laws and trade regulations may result in civil and criminal penalties and any loss or other legal consequences arising from non-compliance will be solely the responsibility of you or your organization. If you or your organization’s non-compliance causes any loss to HONENG ELEC., we will seek legal redress.

By purchasing, selling, using, or otherwise transferring HONENG ELEC. Products, you or your organization agree to comply with all applicable export and re-export control laws and regulations, including the U.S. Export Administration Regulations (“EAR”), administered by the U.S. Department of Commerce’s Bureau of Industry and Security (“BIS”), the International Traffic in Arms Regulations administered by the U.S. State Department’s Directorate of Defense Trade Controls, and trade sanctions administered by the Treasury Department’s Office of Foreign Assets Control (“OFAC”). You and your organization agree that you or your organization will not, directly or indirectly, without any required government authorization, sell, export, re-export, transfer, divert, or otherwise dispose of any HONENG ELEC. Products to:
1.Any country or territory that is not authorized to receive the HONENG ELEC. Products based on their applicable licensing jurisdiction and export control classification;
2.Any country or territory that is a designated state sponsor of terrorism or subject to comprehensive U.S. trade sanctions (including the Entity List, Specially Designated National List, or other applicable U.S. government sanction list, including, but not limited to, Crimea, Cuba, Iran, North Korea, Sudan, Syria, Russia, Belarus, and any additional country or region added in the future);
3.Any person or entity on a U.S. Government list of prohibited person and entity, including but not limited to, the Specially Designated Nationals and Blocked Persons List, Sectoral Sanctions Identifications List, and Foreign Sanctions Evaders List, administered by OFAC; and the Denied Persons List, Entity List, and Unverified List, administered by BIS;
4.Any person or entity with knowledge or reason to know that HONENG ELEC.’s Products will be used for a prohibited end use, including activities related to the research, development, design, manufacture, construction, operation or maintenance of nuclear, chemical, or biological weapons; 
5.Any “military end user” or any person or entity with knowledge or reason to know that HONENG ELEC.’s Products will be used for a “military end use,” as defined and subject to the license requirements in Section 744.21 of the EAR; or any military-intelligence end user, as defined and subject to the license requirements in Section 744.22 of the EAR

<span style="font-weight: bold; font-size: 18px;">Important announcement: </span>
<span style="font-weight: bold; font-size: 18px;">Please ensure you and your organization strictly comply with above statement. HONENG ELEC. reserves the right to immediately terminate any agreement or business relationship without prior notice in the event of any breach of this compliance statement.</span>`,

            zhtext:`深圳市宏能微电子有限公司（以下简称“宏能”）本公司进口芯片的销售政策是完全遵守全球贸易国家的相关法律和法规。为维护和所有客户(包括个人和公司)的长期合作关系，宏能对向所有客户销售的进口芯片的使用事宜，做出如下特别声明：
 
                    请您或您所在企业在使用、销售、出口、再出口或者转让来自宏能购买的进口芯片时，请务必严格遵守美国进出口管制的法律法规。如不遵守美国出口管制法律和贸易法规可能会对您个人或您所在企业造成民事和刑事处罚，如因贵方不遵守美国出口管制法律法规产生的一切损失及其他法律后果，将由您或您所在企业自行承担；如因贵方违反上述规定的行为给宏能造成损失，我们将依法追责。具体而言，请您及您所在企业在使用、销售、出口、再出口或者转让自宏能公司购买的进口芯片时，谨遵如下约定：
                    
                    1.	不向任何被指定支持恐怖主义或受到美国全面贸易制裁的国家或地区（含克里米亚、古巴、伊朗、朝鲜、苏丹、叙利亚、俄罗斯、白俄罗斯以及后续增加的任何国家或者地区）销售、出口来自宏能的进口芯片或者包含宏能公司进口芯片的管制物品；

                    2.	不向美国政府各项禁止清单（含各类实体清单及SDN清单）上的任何个人或实体销售、出口来自宏能进口芯片或者包含宏能进口芯片的管制物品；

                    3.	不得将宏能公司的进口芯片产品用于禁止被使用的最终用途的个人或实体，包括与核武器、化学或生物武器的研发、设计、制造、建造、操作或维护有关的活动，或者任何军事终端用户。

<span style="font-weight: bold; font-size: 18px;">特别声明：</span>
<span style="font-weight: bold; font-size: 18px;">请您及您所在企业严格遵守以上声明，如有违背上述事项，宏能公司将有权随时终止双方的合作及服务而不作任何预先通知。感谢您的配合与支持。</span>`
        }
    },
    created() {
        this.getList()
    },
    methods:{
        getList() {
            this.zhtext = this.zhtext.replace(/\n/g,"<br/>")
            this.entext = this.entext.replace(/\n/g,"<br/>")
            const apiUrl = `${baseUrl}/api/agreement/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                if(response.data.code==200) {
                    response.data.rows.map(item=>{
                        // \n识别不出来换成<br>
                        this.$set(item,'content',item.content?item.content.replace(/\n/g,"<br/>"):"")
                        this.$set(item,'contentEn',item.contentEn?item.contentEn.replace(/\n/g,"<br/>"):"")
                        if(this.$i18n.locale=='zh') {
                            if(item.id==1) {
                                this.text = item.content
                                this.title = item.title
                            }
                        } else {
                            if(item.id==1) {
                                this.text = item.contentEn
                                this.title = item.titleEn
                            }
                        }
                    })
                }
            })
            .catch(error => {
                
            });
        },
    }
}
</script>
<style lang="scss" scoped> 
.d_container{
    // background: #FAFAFA;
    .d_content{
        width: 1200px;
        margin: 0 auto;
        padding-bottom: 60px;
        .b_title{
            font-family: Arial;
            font-weight: bold;
            font-size: 30px;
            color: #333333;
            text-align: center;
            margin-top: 60px;
        }
        .tline{
            width: 100%;
            height: 1px;
            background-color: #ccc;
            margin: 20px 0;
        }
        .s_title{
            font-family: Arial;
            font-size: 18px;
            color: #333333;
            text-align: center;
            margin-bottom: 20px;
        }
        .d_txt{
            margin-bottom: 20px;
        }
        img {
            width: 1200px;
        }
    }
    .d_line{
        width: 100%;
        height: 20px;
        background: #FAFAFA;
    }
}

</style>