<script>
export default{
    data() {
        return{
            activeIndex: '1',
            hoverIndex:'-1',
            showItems:false,
            showItemsBrand:false,
            showItemsQuality:false,
            showItemsAppli:false,
            activeIndex: ""
        }
    },
    created() {
        if(this.$route.meta.currentIndex) {
            this.activeIndex = this.$route.meta.currentIndex
        } else {
            this.activeIndex = parseInt(localStorage.getItem('activeIndex')) || 0
        }
    },
    computed: {
        // this.$t("h.applications"),
        titleList: function () {
            return [this.$t("h.home"), this.$t("h.aboutus"),this.$t('h.linecard'),this.$t("h.qualitycontrol"), this.$t('h.contactus')]
        },
    },
    methods:{
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        globalClick() {
            this.$router.push({name:'GTCS'})
        },
        mouseenter(index) {
            this.hoverIndex = index
            if(index==1) {
                this.showItems = true
            } else if (index==2) {
                this.showItemsBrand = true
            } else if (index==3){
                this.showItemsQuality = true
            } 
            // else if (index==4){
            //     this.showItemsAppli = true
            // }
        },
        mouseleave() {
            this.hoverIndex = '-1'
            this.showItems = false
            this.showItemsBrand = false
            this.showItemsQuality = false
            this.showItemsAppli = false
        },
        changeLangEvent(val){
            if (val == "zh"){
                localStorage.setItem("locale", "zh");
                this.$i18n.locale = localStorage.getItem("locale");
                localStorage.setItem("lang", "ZH");
                location.reload(); //重新加载
            } else if (val == "en"){
                localStorage.setItem("locale", "en");
                this.$i18n.locale = localStorage.getItem("locale");
                localStorage.setItem("lang", "EN");
                location.reload();
            }
        },
        titleClick(index) {
            this.activeIndex = index
            if(index==0) {
                this.$router.push({name:'home'})
            } else if(index==1) {
                this.$router.push({name:'aboutus'})
            } else if(index==2) {
                this.$router.push({name:'linecard'})
            } else if(index==3) {
                // this.$router.push({name:'qualitycontrol'})
                this.$router.push({name:'vdetails'})
            } 
            // else if(index==4) {
            //     this.$router.push({name:'applications'})
            // } 
            else if(index==4) {
                this.$router.push({name:'contactus'})
            }
            localStorage.setItem('activeIndex', index);
        },
        clickputBrand() {
            this.activeIndex = 2
            localStorage.setItem('activeIndex', 2);
            this.showItemsBrand = false
            this.$router.push({name:'putBrand',query: { tip: '2'}})
        },
        clickapplications(val) {
            this.activeIndex = 4
            localStorage.setItem('activeIndex', 4);
            this.showItemsAppli = false
            this.$router.push({name:'applications',query: { title: val}})
        },
        clickV() {
            this.activeIndex = 3
            localStorage.setItem('activeIndex', 3);
            this.showItemsQuality = false
            this.$router.push({name:'vdetails'})
        },
        clickC() {
            this.activeIndex = 3
            localStorage.setItem('activeIndex', 3);
            this.showItemsQuality = false
            this.$router.push({name:'cdetails'})
        },
        clickQ() {
            this.activeIndex = 3
            localStorage.setItem('activeIndex', 3);
            this.showItemsQuality = false
            this.$router.push({name:'qdetails'})
        },
        clickW() {
            this.activeIndex = 3
            localStorage.setItem('activeIndex', 3);
            this.showItemsQuality = false
            this.$router.push({name:'wdetails'})
        },
        clicklinkcard() {
            this.activeIndex = 2
            localStorage.setItem('activeIndex', 2);
            this.showItemsBrand = false
            this.$router.push({name:'linecard',query: { tip: '1'}})
            window.location.reload();
        },
        clickaboutus() {
            this.activeIndex = 1
            localStorage.setItem('activeIndex', 1);
            this.showItems = false
            this.$router.push({name:'aboutus',query: { aboutus: '1'}})
            window.location.reload();
        },
        clickaboutabracon() {
            this.activeIndex = 1
            localStorage.setItem('activeIndex', 1);
            this.showItems = false
            this.$router.push({name:'aboutabracon'})
        },
        clickjoinus() {
            this.activeIndex = 1
            localStorage.setItem('activeIndex', 1);
            this.showItems = false
            this.$router.push({name:'joinus'})
        },
    }
}
</script>
