                        // padding:20px 0 10px 20px;
                        position: absolute;
                        text-align: left;
                        left:-20px;
                        max-height: 0;
                        overflow: hidden;
                        transition: max-height 0.6s ease;
                        div{
                            margin:10px 0 10px 20px;
                        }
                        div:hover{
                            color:#123F7A;
                        }
                    }
                    .hr_citem.show {
                        max-height: 500px; /* 设置一个足够大的值以容纳内容 */
                    }
